#hero__text {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
#hero {
  color: #fff;
}
#hero__text {
  padding: 0;
  text-align: center;
}
@media (min-width: 1200px) {
  #hero__text {
    text-align: right;
  }
}
#hero .name,
#hero .occupation {
  width: 100vw;
}
@media (min-width: 1200px) {
  #hero .name,
  #hero .occupation {
    width: unset;
  }
}
#hero .name {
  font-size: 50px;
}
@media (min-width: 1200px) {
  #hero .name {
    font-size: 60px;
    margin-right: 135px;
  }
}
#hero .occupation {
  font-size: 30px;
  font-weight: normal;
  transform: translateY(-15px);
}
@media (min-width: 1200px) {
  #hero .occupation {
    font-size: 36px;
  }
}
